import React, { useContext } from "react";
import { Redirect, Route } from "react-router-dom";
import {
  IonApp,
  IonIcon,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs,
  IonSplitPane,
} from "@ionic/react";
import { IonReactRouter } from "@ionic/react-router";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "./theme/variables.css";
import LoginPage from "./pages/Login";
import MenuPrivilege from "./components/MenuPrivilege";
import TabRootPage from "./pages/TabRootPage";
import { AuthContext } from "./modules/auth";
import { ConsentPage } from "./pages/ConsentPage";

const App: React.FC = () => {
  const { isAuthenticated } = useContext(AuthContext);
  return (
    <IonApp>
      {isAuthenticated ? (
        <IonReactRouter>
          <IonSplitPane contentId="main">
            <MenuPrivilege />
            <IonRouterOutlet id="main">
              <Route path="/consent" component={ConsentPage} />
              <Route path="/tabs" component={TabRootPage} />
              <Redirect from="/" to="/tabs/life" exact />
            </IonRouterOutlet>
          </IonSplitPane>
        </IonReactRouter>
      ) : (
        <IonReactRouter>
          <Route path="/login" component={LoginPage} exact />
          <Redirect from="/" to="/login" exact />
        </IonReactRouter>
      )}
    </IonApp>
  );
};

export default App;
